@import "variable";
//  モーダル

// バツマーク
.cross {
  position: absolute;
  top: 15px; /* トップからの距離 */
  right: 40px; /* 右からの距離 */
  width: 25px; /* バツマークの幅 */
  height: 25px; /* バツマークの高さ */
  opacity: 0.7; /* 透明度 */
  cursor: pointer; /* マウスオーバー時のカーソルをポインターに */
  z-index: 1000;
  &::before,
  &::after {
    position: absolute;
    left: 11px;
    content: " ";
    height: 25px; /* バツマークの高さ */
    width: 2px; /* バツマークの線の太さ */
    background-color: white; /* バツマークの色 */
  }
  &::before {
    transform: rotate(45deg);
  }
  &::after {
    transform: rotate(-45deg);
  }
  @media screen and (min-width: 800px) {
  }
}
.modal-container-term,
.modal-container-discontinuation {
  position: fixed;
  height: 100vh;
  width: 100vw;
  top: 0;
  left: 0;
  z-index: 100;
  opacity: 0;
  display: none;
  transition: 1s;
  &.active {
    opacity: 1;
    display: table;
  }

  .modal-background {
    width: 100%;
    display: table-cell;
    background: rgba(0, 0, 0, 0.8);
    text-align: center;
    vertical-align: middle;
    font-family: "ryo-gothic-plusn", sans-serif;
    .modal {
      position: relative;
      background: #281329;
      max-width: 800px;
      width: 95%;
      max-height: 80vh;
      margin-bottom: 2rem;
      // padding: auto 2rem;
      padding: 50px;
      display: inline-block;
      border-radius: 3px;
      font-size: 1.3rem;
      font-weight: 300;
      position: relative;
      text-align: left;
      overflow: scroll;
      @media screen and (max-width: 768px) {
        padding: 15px;
        h2 {
          font-size: 1.3rem;
        }
        h4 {
          font-size: 1.2rem;
        }
      }
      h2 {
        font-size: 1.4rem;
        font-weight: 600;
      }
      h4 {
        font-size: 1.2rem;
        line-height: 25px;
        margin-bottom: 5px;
        margin-top: 10px;
      }
      p {
        font-size: 1rem;
        line-height: 22px;
        padding: 1rem 0;
      }
      li {
        font-size: 1rem;
        margin-left: 1rem;
      }
      .modal-svg {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        border-radius: 3px;
        rect {
          stroke: #fff;
          stroke-width: 2px;
          stroke-dasharray: 778;
          stroke-dashoffset: 778;
        }
      }
      .close-area {
        position: relative;
        width: 100%;
        height: 30px;
        content: "";
        display: block;
      }
    }
  }

  .update-memo {
    margin-top: 2rem;
    padding-top: 1rem;
    border-top: 1px solid white;
    font-size: 0.7rem;
  }
}

// 中止基準
.cancel-wrap {
  position: relative;
  background: #2a2a2a;
  max-width: 1200px;
  width: 90%;
  margin: 0 auto;
  max-height: 80vh;
  margin-bottom: 2rem;
  overflow: scroll;
  display: inline-block;
  padding: 2rem;
  @media screen and (max-width: 768px) {
    width: 85vw;
    .scroll {
      width: 90%;
      margin: 0 auto;
      overflow-x: scroll;
      table {
        width: 100%;
        min-width: 600px;
      }
    }
    .scroll::-webkit-scrollbar {
      height: 5px;
    }
    .scroll::-webkit-scrollbar-track {
      background-color: #d5d3d3;
    }
    .scroll::-webkit-scrollbar-thumb {
      background-color: #bd4c69;
    }
  }

  h2 {
    font-size: 1.6rem;
    font-weight: bold;
  }
  .space {
    // padding: 2rem 0;
    font-size: 1rem;
    @media screen and (max-width: 768px) {
      text-align: center;
      font-size: 1rem;
      margin: 0.5em 1.3em;
    }
  }
  .box {
    padding: 0.5em; /*内側余白*/
    border-radius: 50px; /*丸みを調整*/
    color: white;
    font-weight: bold;
    width: 100%;
    display: flex;
    line-height: 1;
    margin-top: 2rem;
    justify-content: center;
    .subtitle {
      position: relative;
      text-align: center;
      padding: 0rem 2rem;
      font-size: 1.8rem;
      @media screen and (max-width: 768px) {
        font-size: 1.2rem;
      }
    }
    .fontsize {
      font-size: 14pt;
      align-self: flex-end;
      @media screen and (max-width: 768px) {
        font-size: 11pt;
      }
    }

    &.blue {
      background-color: #008fce; /*背景の色*/
    }
    &.green {
      background-color: #67af1d; /*背景の色*/
    }
    &.red {
      background-color: #bd4c69; /*背景の色*/
    }
    &.deepblue {
      background-color: #006f83; /*背景の色*/
    }
  }

  .table {
    position: relative;
    margin: 3px auto;
    width: 100%;
    // border-collapse: separate;
    @media screen and (max-width: 768px) {
      font-size: 0.8rem;
    }
    tr {
      &:first-child {
        th {
          &:not(:last-child) {
            border-right: solid 2px #231716;
          }
        }
      }
      &:not(:first-child) {
        th {
          border-bottom: solid 2px #231716;
        }
      }
      &:last-child {
        th {
          border-bottom: solid 2px #f8f8f8 !important;
        }
      }
    }
    th {
      text-align: center;
      //   border-bottom: solid 2px #0091d4;
      padding: 10px 0;
      background-color: #f8f8f8;
      color: #231716;
      &.none {
        background-color: transparent;
        border: none !important;
      }
      &.top {
        padding: 10px 0 5px;
      }
      //   &.bold {
      //     border-bottom: solid 20px #0091d4;
      //     color: #0091d4;
      //     vertical-align: bottom;
      //   }
      &.border {
        border: none;
      }
    }
    td {
      text-align: center;
      border: solid 2px #f8f8f8;
      padding: 10px 0;
      background-color: #44042a;
      position: relative;
      color: #f8f8f8;

      p {
        display: inline-block;
      }
      .check-one {
        position: relative;
        &::after {
          position: absolute;
          content: "※1";
          top: 0;
          right: 0;
          transform: translateX(100%);
          font-size: 0.8rem;
          vertical-align: top;
          color: orange;
        }
      }
      .check-two {
        position: relative;
        &::after {
          position: absolute;
          content: "※2";
          top: 0;
          right: 0;
          transform: translateX(100%);
          font-size: 0.8rem;
          vertical-align: top;
          color: orange;
        }
      }
      .check-three {
        position: relative;
        &::after {
          position: absolute;
          content: "※3";
          top: 0;
          right: 0;
          transform: translateX(100%);
          font-size: 0.8rem;
          vertical-align: top;
          color: orange;
          // @media screen and (max-width: 768px) {
          //   left: 23em;
          // }
        }
      }

      .complement {
        font-size: 1rem;
        @media screen and (max-width: 768px) {
          font-size: 0.8rem;
        }
      }
      span {
        font-size: 1rem;
        vertical-align: top;
        color: red;
      }
      &.none {
        position: relative;
        text-align: center;
        &::after {
          width: 50%;
          height: 50%;
          content: "";
          border-bottom: 1px #f8f8f8 solid;
          position: absolute;
          top: 0;
          left: 50%;
          display: block;
          transform: translateX(-50%);
        }
      }
    }
    .float-box {
      position: relative;
      .float {
        position: absolute;
        top: 40%;
        left: 42%;
        transform: translate(-30%, -50%);
        background-color: #727171;
        padding: 0.1rem 1rem;
        color: white;
        border: none;
        white-space: nowrap;
        font-size: 0.7rem;
        @media screen and (max-width: 768px) {
          left: 42%;
          padding: 0.1rem 1rem;
        }
      }
    }
    .float-box2 {
      position: relative;
      .float {
        position: absolute;
        top: 41%;
        left: 50%;
        transform: translate(-30%, -50%);
        background-color: #727171;
        padding: 0.1rem 6rem;
        color: white;
        border: none;
        white-space: nowrap;
        font-size: 0.7rem;
        @media screen and (max-width: 768px) {
          white-space: nowrap;
          padding: 0.1rem 1rem;
        }
      }
    }
  }
  .text2 {
    margin-top: 3em;
    font-size: 1rem;
    color: #f8f8f8;
    text-align: justify;
    @media screen and (max-width: 768px) {
      font-size: 10px;
    }
  }
  .title-black {
    text-align: center;
    color: rgb(29, 29, 29);
    font-size: 30px;
    margin: 3em 0em 0em;
    @media screen and (max-width: 768px) {
      font-size: 20px;
      margin: 1em 0em 0em;
    }
  }

  video {
    width: 960px;
    height: 550px;
    margin: 1em;
    overflow: hidden;
    @media screen and (max-width: 768px) {
      height: 250px;
      margin: 5px 5px 20px;
    }
  }

  .check-area {
    display: flex;
    justify-content: center;
  }
}
